___CSS_LOADER_EXPORT___.push([module.id, `.loading {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0042255e;
  transition: opacity 0.75s, visibility 0.75s;
  z-index: 100;
}

.loading-hidden {
  opacity: 0;
  visibility: hidden;
}

.loading::after {
  content: '';
  width: 75px;
  height: 75px;
  border: 15px solid var(--crim);
  border-radius: 50%;
  border-top: 15px solid var(--hijau);
  animation: loading 0.75s ease infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}
