___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --hijau: #004225;
  --crim: #f5f3dc;
  --orange: #ffb000;
  --pink: #fce0c2;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--crim);
  font-family: 'Rowdies', cursive;
}

error-element {
  display: none;
  width: 100%;
}

footer {
  width: 100%;
  background-color: var(--hijau);
  margin-top: -1rem;
  padding-top: 1rem;
  color: var(--crim);
  font-size: 17px;
  text-align: center;
  height: 50px;
}

.skip-link {
  position: absolute;
  top: -47px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 12px;
  font-size: 18px;
  z-index: 60;
  display: block;
}

.skip-link:focus {
  top: 0;
  left: 0;
}

@media screen and (max-width: 576px) {
  footer {
    font-size: 12px;
  }
}
