// Module
___CSS_LOADER_EXPORT___.push([module.id, `.search-form {
  display: flex;
  background-color: var(--hijau);
  padding: 1rem;
  width: 70%;
  max-width: 700px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px var(--hijau);
}

#search-input {
  padding: 10px 20px;
  width: 80%;
  font-family: 'Rowdies', cursive;
  font-size: 18px;
  border-radius: 20px 0 0 20px;
  border: 1px solid var(--orange);
}

#search-input:focus {
  outline: 1px solid var(--orange);
}

#search-btn {
  padding: 10px;
  width: 20%;
  border-radius: 0 20px 20px 0;
  border: 1px solid var(--orange);
  background-color: var(--orange);
  cursor: pointer;
  font-family: 'Rowdies', cursive;
  font-size: 18px;
  font-weight: 500;
  transition: 0.2s;
}

#search-btn:hover,
#search-btn:active {
  box-shadow: 0px 0px 5px var(--orange);
  font-weight: bold;
}

.search-container {
  width: 100%;
  padding: 1rem 0;
}

#showSearchResto {
  width: 100%;
  padding: 0 4rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

@media screen and (max-width: 992px) {
  #showSearchResto {
    padding: 0 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 768px) {
  #showSearchResto {
    padding: 0 1rem;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 576px) {
  #showSearchResto {
    padding: 0 1rem;
    grid-template-columns: 1fr;
  }

  .search-container {
    padding: 1rem;
  }

  .search-form {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }

  #search-input {
    margin-bottom: 10px;
    width: 100%;
    border-radius: 5px;
  }

  #search-btn {
    width: 50%;
    border-radius: 5px;
  }
}
