___CSS_LOADER_EXPORT___.push([module.id, `.hero {
  min-width: 100%;
  height: 100vh;
}

.hero-tagline {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 2rem;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: var(--orange);
  z-index: 20;
}

.hero-tagline h2 {
  font-size: 50px;
  text-shadow: 0px 0px 10px black;
}

.hero-tagline p {
  font-size: 20px;
  text-shadow: 0px 0px 10px black;
  padding-bottom: 2rem;
}

search-resto {
  width: 100%;
  display: flex;
  justify-content: center;
}

.hero-slider {
  overflow: hidden;
}

.slider-item {
  position: relative;
  width: 500%;
  margin: 0;
  top: 0;
  left: 0;
  animation: 15s slider infinite;
}

.slider-item img {
  width: 20%;
  height: 100vh;
  object-fit: cover;
  float: left;
}

@keyframes slider {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}

@media screen and (max-width: 576px) {
  .hero-tagline h2 {
    font-size: 30px;
  }

  .hero-tagline p {
    font-size: 15px;
  }
}
